.App{
  height: 100%;
  width: 90%;
  margin: 0 5%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}
.items{
  height: 150px;
  width: 100px;
  margin: 50px 5vw;
  /*background-color: #d8f3d4;*/
  /*background-color: rgba(216,243,212,0.6);*/
  position: relative;
}
.items:after{
  content: '';
  position: absolute;
  bottom:50px;
  left:0;
  width:100%;
  height:10px;
  border-bottom:1px solid #f6b198;
  -webkit-transform:scaleY(.5);
  transform:scaleY(.5);
}
.content{
  height: 100px;
  width: 100px;
  /*background-color: #d8f3d4;*/
  background-color: rgba(216,243,212,0.6);
  border-radius: 5px 5px 0 0;
  position: absolute;
  top: 0;
  left: 0;
  line-height: 100px;
}
.title{
  height: 50px;
  width: 100px;
  /*background-color: #d8f3d4;*/
  background-color: rgba(216,243,212,0.6);
  border-radius: 0 0 5px 5px;
  position: absolute;
  bottom: 0;
  left: 0;
  line-height: 50px;
  text-align: center;
}